.backtop {
  @extend %link;

  position: fixed;
  bottom: 10px;
  right: 10px;
  z-index: 11;

  display: flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;

  color: white;
  background-color: var(--color-accent);

  border-radius: 50%;
  opacity: 0.3;

  transition: opacity var(--transition-params);

  &:hover,
  &:focus-visible {
    opacity: 1;
  }

  &--hidden {
    display: none;
  }

  // &__icon {
  //   width: 50%;
  //   height: 50%;
  //   fill: currentColor;

  //   transform: rotate(-90deg);
  // }
}
